.Board {
    position: relative;
    margin: 0 auto;
    background-color: #000;
    background-image:
        -webkit-linear-gradient(#333 1px, transparent 1px),
        -webkit-linear-gradient(left, #333 1px, transparent 1px);
    background-image:
        -o-linear-gradient(#333 1px, transparent 1px),
        -o-linear-gradient(left, #333 1px, transparent 1px);
    background-image:
        linear-gradient(#333 1px, transparent 1px),
        linear-gradient(90deg, #333 1px, transparent 1px);
}

.Cell {
    background: #ccc;
    position: absolute;
}

.controls {
    margin-top: 20px;
    position: fixed;
    bottom: 20px;
    padding: 20px;
    background-color: #fff;
}

.button {
    margin-left: 12px;
    cursor: pointer;
}

.BoardCont{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    overflow: scroll;

}
select {
    margin-left: 10px;
}
.container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
}


